import Vue from 'vue'
import VueRouter from 'vue-router'
// 按钮加载路由
const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Login.vue')
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home.vue')
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome.vue')
// import Login from '../components/Login.vue'
// import Home from '../components/Home.vue'
// import Welcome from '../components/Welcome.vue'
const User = () => import(/* webpackChunkName: "user_right_role" */ '../components/user/User.vue')
const Right = () => import(/* webpackChunkName: "user_right_role" */ '@/components/right/Rights.vue')
const Role = () => import(/* webpackChunkName: "user_right_role" */ '@/components/right/Roles.vue')
// import User from '../components/user/User.vue'
// import Right from '@/components/right/Rights.vue'
// import Role from '@/components/right/Roles.vue'
const Categories = () => import(/* webpackChunkName: "cate_params" */ '@/components/shop/Categories.vue')
const Params = () => import(/* webpackChunkName: "cate_params" */ '@/components/shop/Params.vue')
// import Categories from '@/components/shop/Categories.vue'
// import Params from '@/components/shop/Params.vue'
const List = () => import(/* webpackChunkName: "list_good" */ '@/components/shop/List.vue')
const Good = () => import(/* webpackChunkName: "list_good" */ '@/components/shop/Good.vue')
// import List from '@/components/shop/List.vue'
// import Good from '@/components/shop/Good.vue'
const Order = () => import(/* webpackChunkName: "order_report" */ '@/components/order/Orders.vue')
const Report = () => import(/* webpackChunkName: "order_report" */ '@/components/report/Report.vue')
// import Order from '@/components/order/Orders.vue'
// import Report from '@/components/report/Report.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  { path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      { path: '/welcome', component: Welcome },
      { path: '/users', component: User },
      { path: '/rights', component: Right },
      { path: '/roles', component: Role },
      { path: '/categories', component: Categories },
      { path: '/params', component: Params },
      { path: '/goods', component: List },
      { path: '/goods/add', component: Good },
      { path: '/orders', component: Order },
      { path: '/reports', component: Report }
    ] }
]
const router = new VueRouter({
  mode: 'history',
  routes
})
// a路由向b路由跳转的时候会执行
// 导航守卫  防止非法访问 不登录就访问
router.beforeEach((to, from, next) => {
  // to  去哪 clg
  // console.log(to)
  // from 来自哪里
  // console.log(from)
  // 去登录页面就放行
  if (to.path == '/login') return next()
  // 不是的话就检查有没有token
  const tokenStr = window.sessionStorage.getItem('token')
  // 没有的话 就让它去登录
  if (!tokenStr) return next('/login')
  // 有的话放行
  next()
})

// 本质就是改写了element内部的push方法
// 对错误进行了捕获
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}
export default router
